import React, { Component} from "react"
import Producto from './Producto.js'


import {

  Card,
  Button,
  Modal,
  FormGroup,
  Row,
  Col,
  Form
} from "react-bootstrap";

class Seccion extends Component{
  constructor(){
    super();
    this.state={
    nombreProducto:"",
    descripcion:"",
    precio:"",
    modalActualizar: false,
    mostrarProductos: false}
  this.handleInput=this.handleInput.bind(this)
  this.handleSubmit=this.handleSubmit.bind(this)
  this.modalActualizar=this.modalActualizar.bind(this)
  this.mostrarProductos=this.mostrarProductos.bind(this)

}  
    handleInput(e){
    e.preventDefault()
    const {value, name} =e.target 
    this.setState({
      [name]: value}) }

    handleSubmit(id){
      const idp=Math.floor(Math.random() * 10000) + 1
      const nuevoproducto={idp:idp,nombreProducto: this.state.nombreProducto, descripcion: this.state.descripcion, precio:this.state.precio, grupoOpciones:[]}
      this.props.add("producto",nuevoproducto,id)}  

    modalActualizar(show){
      this.setState({modalActualizar: show})
        console.log(this.state.modalActualizar)
       };

    mostrarProductos(){
      const mp=this.state.mostrarProductos
      this.setState({ 
        mostrarProductos: !mp});
    };
    render(){


return (
  <Card>
  <Card.Header>
    <div>
    <Row>
  <Col><h4 className="col">{this.props.seccion.nombreSeccion}</h4></Col>               
  <Col style={{textAlign: "rigth"}}>
  <Button variant="primary" onClick={this.modalActualizar.bind(this,true)}>Editar</Button> {' '}
  <Button  variant="danger" onClick={this.props.remove.bind(this,"seccion",this.props.seccion.ids)}>Borrar</Button> {' '}</Col> 
  <Button size="sm" variant="light" onClick={this.mostrarProductos.bind(this)}>{this.state.mostrarProductos? "Ocultar Productos": "Mostrar Productos"}</Button> 
  
  </Row>
  
  </div>
  </Card.Header>
  <Modal show={this.state.modalActualizar} >
  <Modal.Header>
   <div><h3>Editar Registro</h3></div>
  </Modal.Header>
  <Modal.Body >
    <FormGroup>
      <label>
        Nombre seccion: 
      </label>
      <input
        className="form-control"
        name="nombreSeccion"
        type="text"
        onChange={this.handleInput}
        placeholder={this.props.seccion.nombreSeccion}
      />
    </FormGroup>
  </Modal.Body>
  <Modal.Footer>
    <Button
      variant="success"
      onClick={() => {this.props.edit("seccion",this.state.nombreSeccion,this.props.seccion.ids);this.modalActualizar(false)}}
    >
      Editar
    </Button>
    <Button
      variant="danger"
      onClick={() => this.modalActualizar(false)}
    >
      Cancelar
    </Button>
  </Modal.Footer>
</Modal>       

  {this.state.mostrarProductos &&
(<Card.Body>
   {this.props.seccion.productos.map((producto)=> {return <Producto  key={producto.idp} producto={producto} edit={this.props.edit} 
   cambio={this.props.cambiop}  remove={this.props.remove} ids={this.props.seccion.ids} removeo={this.props.removeopcion} 
   add={this.props.add} />})}
  
<Form.Group style={{padding: "1rem"}}>
<Row  >
<Col><input type="text" required onChange={this.handleInput} name="nombreProducto" className="form-control" placeholder="Nombre de Producto"></input></Col>
<Col><input type="number" required min="0" onChange={this.handleInput} name="precio" className="form-control" placeholder="Precio"></input></Col>
</Row>
<Row style={{padding: "1rem"}} >
<input type="text" required onChange={this.handleInput} name="descripcion" className="form-control" placeholder="Descripción de Producto"></input>
</Row>
<Button type="button"  variant="success" onClick={this.handleSubmit.bind(this,this.props.seccion.ids)}>Agregar Nuevo Producto</Button> 
</Form.Group>
</Card.Body> )}
    
 
    </Card>
   
  )     


}}
export default Seccion;