import React, { Component} from "react"
import GrupoOpciones from './GrupoOpcion.js'
import {
  Container,

  Button,
  Modal,
  Form,

  Row,
  Col
} from "react-bootstrap";



class Producto extends Component{
  constructor(){
    super();
    this.state={
    nombreGrupo:"",
    cantidadMinima:"",
    modalActualizar: false,
    mostrarOpciones:false}
  this.handleInput=this.handleInput.bind(this)
  this.handleSubmit=this.handleSubmit.bind(this)
  this.modalActualizar=this.modalActualizar.bind(this)
  this.mostrarOpciones=this.mostrarOpciones.bind(this)
  
 }  
    handleInput(e){
    const {value, name} =e.target 
    this.setState({
      [name]: value
    })
    }
    handleSubmit(ids,idp){
      const id=Math.floor(Math.random() * 10000) + 1
      const nuevogrupo={idg:id,nombreGrupo: this.state.nombreGrupo,cantidadMinima:this.state.cantidadMinima,cantidadMaxima:this.state.cantidadMaxima,modificaPrecio:this.state.modificaPrecio,opciones:[]}
      this.props.add("grupoOpciones",nuevogrupo,ids,idp)
    }    
    modalActualizar(show){
        this.setState({modalActualizar: show})
          
         };
    mostrarOpciones(){
        const mp=this.state.mostrarOpciones
        this.setState({ 
          mostrarOpciones: !mp});
      };
    render(){ 
        
return(<Container>
  <Row style={{padding: "0.5rem"}}> 
  <Col><h5>{this.props.producto.nombreProducto}</h5></Col> 
  <Col><h5>Precio: {this.props.producto.precio}</h5></Col>
  <Col>
  <Button variant="primary" size="sm" onClick={this.modalActualizar.bind(this,true)}>Editar</Button>{' '}
  <Button variant="danger" size="sm" onClick={this.props.remove.bind(this,"producto",this.props.ids,this.props.producto.idp)}>Borrar</Button> </Col> 
  <Button variant="light" size="sm" onClick={this.mostrarOpciones.bind(this)}>{this.state.mostrarOpciones? "Ocultar Grupos de Opciones": "Mostrar Grupos de Opciones"}</Button>  {' '}
    
 
  </Row >
<Modal show={this.state.modalActualizar} >
<Modal.Header><div><h3>Editar Registro</h3></div> </Modal.Header>
<Modal.Body ><Form.Group>
    <label>Nombre Producto: </label>
    <input className="form-control" name="nombreProducto" type="text" onChange={this.handleInput} placeholder={this.props.producto.nombreProducto}/>
  </Form.Group>
  <Form.Group>
    <label>Descripcion: </label>
    <input className="form-control"name="descripcion"type="text"onChange={this.handleInput}placeholder={this.props.producto.descripcion}/>
  </Form.Group>
  <Form.Group>
    <label>Precio: </label>
    <input className="form-control"name="precio" type="number" min="0"onChange={this.handleInput}placeholder={this.props.producto.precio}/>
  </Form.Group>
</Modal.Body>
<Modal.Footer>
  <Button variant="success"onClick={() => {this.props.edit("producto",this.state,this.props.ids,this.props.producto.idp);this.modalActualizar(false)}}>Editar</Button>
  <Button variant="danger"onClick={() => this.modalActualizar(false)}>Cancelar</Button>
</Modal.Footer>
</Modal>
{this.state.mostrarOpciones &&
<Container>
{this.props.producto.grupoOpciones.map((grupo)=> {return <GrupoOpciones key={grupo.idg} grupo={grupo} ids={this.props.ids} idp={this.props.producto.idp} remove={this.props.remove} add={this.props.add} edit={this.props.edit}/>})}
<Form.Group  style={{padding: "0.4rem"}}>
         <Row style={{padding: "0.25rem"}}>
          <input type="text" onChange={this.handleInput} name="nombreGrupo" className="form-control" placeholder="Nombre del Grupo de Opciones"></input>
          </Row>
          <Row style={{padding: "0.1rem"}}>
          <Col><input type="number" min="0" onChange={this.handleInput} name="cantidadMinima" className="form-control" placeholder="Cantidad Minima"></input></Col>
          <Col><input type="number" min="0" onChange={this.handleInput} name="cantidadMaxima" className="form-control" placeholder="Cantidad Maxima"></input></Col>
          </Row>
          <Form.Check name="modificaPrecio" onChange={this.handleInput} type="checkbox" label="ModificaPrecio" />
          <button  type="button" className="btn btn-success" onClick={this.handleSubmit.bind(this,this.props.ids,this.props.producto.idp)}>Agregar Nuevo Grupo de Opciones</button>    
</Form.Group>
</Container>}
</Container>)

}}
export default Producto;