import React, { Component} from "react"
import {

  Card,
  Modal,
  Col,
  FormGroup,
  Form,
  Row,
  Button

} from "react-bootstrap";
import Opcion from "./Opcion"


class GrupoOpciones extends Component{
  constructor(){
    super();
    this.state={
    nombreOpcion:"",
    precioAdicional:"",
    modalActualizar: false,
    mostrarOpciones:false}

  this.handleInput=this.handleInput.bind(this)
  this.handleSubmit=this.handleSubmit.bind(this)
  this.modalActualizar=this.modalActualizar.bind(this)
  this.mostrarOpciones=this.mostrarOpciones.bind(this)

}  

    handleInput(e){
    e.preventDefault()
    const {value, name} =e.target 
    this.setState({
      [name]: value
    })
    }
    handleSubmit(ids,idp,idg){
      const ido=Math.floor(Math.random() * 10000) + 1
      const nuevaopcion={ido:ido, nombreOpcion: this.state.nombreOpcion, precioAdiconal: this.state.precioAdiconal}
      this.props.add("opcion",nuevaopcion,ids,idp,idg)
    }  
    modalActualizar(show){
      this.setState({modalActualizar: show})
        console.log(this.state.modalActualizar)
       };
    mostrarOpciones(){
      const mp=this.state.mostrarOpciones
      this.setState({ 
        mostrarOpciones: !mp});
    };
    render(){

return(
 <div>
   <div>
   <Row>
 <Col><h5 className="col">{this.props.grupo.nombreGrupo}</h5></Col>
 <Col ><h5 className="col">Min: {this.props.grupo.cantidadMinima}  Max: {this.props.grupo.cantidadMaxima}</h5></Col>     
 
 <Col>
 <Button variant="primary" size="sm" onClick={this.modalActualizar.bind(this,true)}>Editar</Button> {' '}
 <Button  variant="danger"size="sm"  onClick={this.props.remove.bind(this,"grupoOpciones",this.props.ids,this.props.idp,this.props.grupo.idg)}>Borrar</Button></Col>  
 <Button variant="light"  size="sm" onClick={this.mostrarOpciones.bind(this)}>{this.state.mostrarOpciones? "Ocultar Opciones": "Mostrar Opciones"}</Button> {' '}
 
 </Row>
 
 </div>

 <Modal show={this.state.modalActualizar} >
 <Modal.Header>
  <div><h3>Editar Registro</h3></div>
 </Modal.Header>
 <Modal.Body >
   <FormGroup>
     <label>
       Nombre Grupo de Opciones: 
     </label>
     <input
       className="form-control"
       name="nombreGrupo"
       type="text"
       onChange={this.handleInput}
       placeholder={this.props.grupo.nombreGrupo}
     />
   </FormGroup>
   <FormGroup>
     <label>
       Cantidad Minima: 
     </label>
     <input
       className="form-control"
       name="cantidadMinima"
       type="number" min="0"
       onChange={this.handleInput}
       placeholder={this.props.grupo.cantidadMinima}
     />
   </FormGroup>
   <FormGroup>
     <label>
       Cantidad Maxima: 
     </label>
     <input
       className="form-control"
       name="cantidadMaxima"
       type="number" min="0"
       onChange={this.handleInput}
       placeholder={this.props.grupo.cantidadMaxima}
     />
   </FormGroup>
   <Form.Group controlId="formBasicCheckbox">
    <Form.Check name="modificaPrecio" onChange={this.handleInput} type="checkbox" label="ModificaPrecio" />
  </Form.Group>
  
 </Modal.Body>
 <Modal.Footer>
   <Button
     variant="success"
     onClick={() => {this.props.edit("grupoOpciones",this.state,this.props.ids,this.props.idp,this.props.grupo.idg);this.modalActualizar(false)}}
   >
     Editar
   </Button>
   <Button
     variant="danger"
     onClick={() =>this.modalActualizar(false)}
   >
     Cancelar
   </Button>
 </Modal.Footer>
</Modal>       

 {this.state.mostrarOpciones &&
(<Card.Body>
  {this.props.grupo.opciones.map((opcion)=> {return <Opcion  key={opcion.ido} opcion={opcion} ids={this.props.ids} idp={this.props.idp} idg={this.props.grupo.idg} remove={this.props.remove}/>})}
 <Form.Group style={{padding: "0.1rem"}}>
        <Row style={{padding: "0.25rem"}}>
        <Col><input type="text" onChange={this.handleInput} name="nombreOpcion" className="form-control" placeholder="Nombre de la Opcion"></input></Col>
        <Col><input type="number" min="0" onChange={this.handleInput} name="precioAdiconal" className="form-control" placeholder="Precio"></input></Col>
        </Row>
<Button  type="button" variant="success" onClick={this.handleSubmit.bind(this,this.props.ids,this.props.idp,this.props.grupo.idg)}>Agregar Nueva Opción</Button> 
</Form.Group>
</Card.Body>
)}

</div>
)
}}
export default GrupoOpciones;