import React, { Component } from 'react';

class Formulario extends Component {
    constructor(){
      super();
      this.state={
      nombreSeccion:""
    }
    this.handleInput=this.handleInput.bind(this)
    this.handleSubmit=this.handleSubmit.bind(this)
   }  

   handleInput(e){
    e.preventDefault()
    const {value, name} =e.target 
    this.setState({
      [name]: value
    })
   }
   
   handleSubmit(){
    const id=Math.floor(Math.random() * 10000) + 1
    const nuevaseccion={ids:id,nombreSeccion:this.state.nombreSeccion,productos:[]}
    this.props.add("seccion",nuevaseccion)}
   

   render() {
    const seccion= 
      <div className="card" >
       <form className="card-body" >
          <div className="form-group" >
           <input type="text" onChange={this.handleInput}   name="nombreSeccion" className="form-control" placeholder="Nombre de Sección"></input>
          <button type="button" className="btn btn-primary" onClick={this.handleSubmit.bind(this)} >Agregar Nueva Seccion</button>
          </div>
          </form>
         
       </div> 
      
    return (
      
seccion
    )
  }
}export default Formulario;