import React, { Component} from "react"
import {

  Button,
  Col,
  Row

} from "react-bootstrap";

class Opcion extends Component{

    render(){


      return (
        <div>
        <Row style={{padding: "0.1rem"}}>
        <Col>
           {this.props.opcion.nombreOpcion}</Col>
        <Col>
          Precio: {this.props.opcion.precioAdiconal}
        </Col>
        <Col>
  
           <Button variant="danger" size="sm"
                onClick={this.props.remove.bind(this,"opcion",this.props.ids,this.props.idp,this.props.idg,this.props.opcion.ido)}>
               Borrar
             </Button> 
             </Col>
        </Row>
        </div>)
   
}}
export default Opcion;